export default {
	data() {
		return {
			popupIndex: false,
			token:'',
			signList:{},
			datas:[],
			datas2:[],
			goesCotnet:{}
		}
	},
	created:function(){ 
		var _this = this;
		uni.getStorage({
			 key: 'token',
			 success: function (res) {
				 _this.token = res.data
			}
		})
	},
	mounted: function () {
		var _this = this;
		_this.signIns();
	},
	methods: {
		signIn() {
			var _this = this;
			if (_this.popupIndex) {
				_this.popupIndex = false
			} else{
				_this.popupIndex = true
				let data={
					channel:1,
					token:_this.token,
				};
				_this.$axios(_this.$baseUrl.userSign,data).then(res =>{
					if(res.data.status==1){
						_this.goesCotnet = res.data.result;
						_this.signIns();
					}
				}).catch(error =>{
					
				})
			}
			
		},
		signIns:function(){
			let _this=this;
			let data={
				channel:1,
				token:_this.token,
			};
			_this.$axios(_this.$baseUrl.signIn,data).then(res =>{
				if(res.data.status==1){
					_this.signList = res.data.result;
					_this.datas = res.data.result.data;
					_this.datas2 = res.data.result.is_sign;
				}
			}).catch(error =>{
				
			})
		},
		userSign:function(){
			
		},
	}
}
